html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Overpass", sans-serif;
  font-size: 0.875rem;
  background-color: hsl(216deg, 12%, 8%);
}

ul {
  margin: 0;
}

li {
  list-style: none;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.hide {
  display: none;
}

.text-ac {
  text-align: center;
}

.btn {
  background-color: hsl(213deg, 15%, 23%);
  color: hsl(217deg, 12%, 63%);
}

.active {
  background-color: hsl(217deg, 12%, 63%);
  color: white;
}

.attribution {
  font-size: 11px;
  text-align: center;
  color: white;
}

.attribution a {
  color: hsl(228deg, 45%, 44%);
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.main {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main__container {
  background-color: hsl(213deg, 20%, 18%);
  max-width: 20.5rem;
  padding: 1.625rem;
  border-radius: 1rem;
  height: 21.6875rem;
  margin: 1rem;
}
.main__star {
  background-color: hsl(213deg, 15%, 23%);
  padding: 0.625rem;
  border-radius: 50%;
  margin-bottom: 1rem;
}
.main__h1 {
  font-weight: 700;
  color: hsl(0deg, 0%, 100%);
  font-size: 1.5rem;
  margin-bottom: 0.75rem;
}
.main__text {
  color: hsl(217deg, 12%, 63%);
  margin-bottom: 1.875rem;
}
.main__text__rating {
  margin-bottom: 0px;
}
.main__ul {
  display: flex;
  padding: 0;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  user-select: none;
}
.main__ul li {
  width: 2.625rem;
  height: 2.625rem;
  line-height: 2.625rem;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
  transition: 100ms ease-in-out;
}
.main__ul li:hover {
  background-color: hsl(25deg, 97%, 53%);
  color: white;
}
.main__button {
  width: 100%;
  padding: 0.75rem 0;
  background-color: hsl(25deg, 97%, 53%);
  border-style: none;
  border-radius: 1.5rem;
  font-family: "Overpass", sans-serif;
  font-weight: 700;
  font-size: 0.9375rem;
  color: hsl(0deg, 0%, 100%);
  margin-bottom: 0.375rem;
  cursor: pointer;
  transition: 100ms ease-in-out;
}
.main__button:hover {
  background-color: hsl(25deg, 97%, 63%);
}
.main__button:active {
  background-color: hsl(0deg, 0%, 100%);
  color: hsl(25deg, 97%, 53%);
  transition: none;
}
.main__rating-text {
  color: hsl(25deg, 97%, 53%);
  background-color: hsl(213deg, 15%, 23%);
  display: inline-block;
  padding: 0.375rem 0.75rem;
  border-radius: 1.125rem;
  margin-bottom: 1.625rem;
}
.main__mobileimg {
  width: 50%;
  margin-bottom: 1.25rem;
  margin-top: 0.875rem;
}